import * as React from 'react';
import * as moment from 'moment';
import * as classNames from 'classnames';

import {Match} from '../../Data/Matches';
import {timeNow} from '../../Utils/TimeUtil';
import {
    UI_MENU_DATE_FORMAT
} from '../../Utils/DateUtil';

import './Nav.css';

interface NavProps {
    title: string;
    navOpen: boolean;
    matches: Match[];
    selectedMatch: Match;
    onToggleNav: () => void;
    onSelectMatch: (match: Match) => void;
}

export default class Nav extends React.PureComponent<NavProps> {
    constructor(props: NavProps) {
        super(props);
        this.toggleNav = this.toggleNav.bind(this);
        this.onSelectMatch = this.onSelectMatch.bind(this);
    }

    render() {
        const sideNavStyles = this.props.navOpen ?
            { transform: 'translateX(0px)' } :
            { transform: 'translateX(-100%)' };

        return (
            <nav className="nav">
                <div className="nav__bar">
                    <div className="nav__toggle" onClick={this.toggleNav}>
                        <i className="nav__toggle_icon material-icons">menu</i>
                    </div>
                    <div className="nav__title_container">
                        <div className="nav__title">
                            {this.props.title}
                        </div>
                    </div>
                </div>
                <div style={sideNavStyles} className="nav__menu">
                    <ul>
                        <li className="nav__toggle" onClick={this.toggleNav}>
                            <i className="nav__toggle_icon material-icons">close</i>
                        </li>
                        {this.renderMatchList()}
                    </ul>
                </div>
            </nav>
        );
    }

    private toggleNav() {
        this.props.onToggleNav();
    }

    private onSelectMatch(match: Match) {
        this.props.onSelectMatch(match);
    }

    private renderMatchList() {
        return this.props.matches.map((match, index) => {
            const time = moment(match.date).utcOffset(match.ground.state.timeZone * 60).local();
            const selected: boolean = match.matchNumber === this.props.selectedMatch.matchNumber;
            const complete: boolean = time.clone().add(3, 'hours').isBefore(timeNow());
            const listClasses = classNames(
                'nav__item',
                {
                    'nav__item--selected': selected,
                    'nav__item--complete': complete
                }
            );
            return (
                <li
                    className={listClasses}
                    key={index + '-' + match.date.toDateString()}
                    onClick={() => this.onSelectMatch(match)}
                >
                    <div className="nav__item_container">
                        <div className="nav__item_number">
                            {match.matchNumber} :
                        </div>
                        <div className="nav__item_details">
                            <div className="nav__item_teams">
                                {match.homeTeam.shortName} -vs- {match.awayTeam.shortName}
                            </div>
                            <div className="nav__item_venue">
                                {match.ground.name}, {match.ground.suburb}, {match.ground.state.name}
                            </div>
                            <div className="nav__item_date_time">
                                {time.format(UI_MENU_DATE_FORMAT)}
                            </div>
                        </div>
                    </div>
                </li>
            );
        });
    }
}


